.btn.btn-secondary {
  color: #fff;
  background-color: #6c757d;
  border-color: #6c757d;
}
.btn.btn-secondary.focus,
.btn.btn-secondary:focus {
  color: #fff;
  background-color: #6c757d;
  border-color: #6c757d;
  outline: none;
  box-shadow: none;
}
.btn.btn-secondary:hover {
  color: #fff;
  background-color: #6c757d;
  border-color: #6c757d;
  outline: none;
  box-shadow: none;
}
.btn.btn-secondary.active,
.btn.btn-secondary:active {
  color: #fff;
  background-color: black!important;
  outline: rgba(108, 117, 125, 0.7) solid 3px !important;
}
.btn.btn-secondary.active.focus,
.btn.btn-secondary.active:focus,
.btn.btn-secondary.active:hover,
.btn.btn-secondary:active.focus,
.btn.btn-secondary:active:focus,
.btn.btn-secondary:active:hover {
  color: #fff;
  background-color: #6c757d;
  border-color: #6c757d;
  outline: none;
  box-shadow: none;
}