.list-item { 
    background-color: #343a40;
    cursor: pointer;
}

.list-item:hover {
    background-color: #7d8b99;
}

.list-item-icon > img {
    max-width: 70px;
    max-height: 70px;
}

.list-item-icon {
    background-color: #65696e;
    padding: 10px;
    width: 80px;
    height: 80px;
}

.list-item-name {
    color: #90ff00;
    font-weight: 600;
    font-size: 16px;
    white-space: normal;
    line-height: 1.5;
    height: 50px;
}

.list-item-info { 
    font-size: 12px;
    color: rgba(255, 255, 255, 0.8);
}